function isImageFile(filePath: string) {
  if (!filePath) return false
  const extension = filePath.split('.').pop()?.toLowerCase()
  return ['jpg', 'jpeg', 'png', 'gif', 'webp'].includes(extension || '')
}

function openFile(filePath: string) {
